.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding-block: 32px 16px;
  padding-inline: 16px;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: auto;
  display: block;
}

.card__title {
  color: #060b35;
  margin-block: 40px 0;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  margin-block: 4px 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 8px;
  display: flex;
}

.card__price-text {
  color: #616070;
  align-items: center;
  font-size: 12px;
  line-height: 18px;
}

.card__price-sum {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-weight: 800;
}

.card__button:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.stars {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars__reviews {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars--1 .stars__star:nth-child(n+2), .stars--2 .stars__star:nth-child(n+3), .stars--3 .stars__star:nth-child(n+4), .stars--4 .stars__star:nth-child(n+5), .stars--5 .stars__star:nth-child(n+6) {
  background-image: url("star.822d561e.svg");
}

html {
  font-family: Roboto, sans-serif;
}

body {
  box-sizing: border-box;
  margin: 0;
}
